
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  padding: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.user-list {
  width: 50%;
  margin-right: auto;
}
.user-list__add-btn {
  display: block;
  margin-left: auto;
}
.user-list__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  list-style: none;
  margin: 0;
  margin-top: 10px;
  padding: 0;
  border-right: solid #000 1px;
  border-bottom: solid #000 1px;
}
.user-list__item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-left: solid #000 1px;
  border-top: solid #000 1px;
}
.user-list__title {
  cursor: pointer;
  padding: 10px;
  width: 50%;
  margin: 0;
  border-left: solid #000 1px;
};
.user-list__title--phone {
  border-left: solid #000 1px;
}
.user-list__empty-title {
  margin-bottom: -30px;
}


.main-btn {
    cursor: pointer;
    padding: 10px 30px;
    border-radius: 15px;
    background: lightgrey;
    border: 1px #000 solid;
}
.main-btn:hover {
    opacity: 0.5;
}

.user--name {
  border-right: solid #000 1px;
}
.user {
  text-align: left;
  padding: 10px;
  width: 50%;
}

.add-user {
    position: relative;
    width: 30%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-top: 47px;
    margin-right: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    border: 1px solid #000;
    padding: 10px 20px 20px 20px;
}
.add-user__header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 40px;
}
.add-user__input-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
}
.add-user__input-list li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    margin-bottom: 10px;
    padding-left: 1px;
}
.add-user__input-list li input{
    width: 40%;
}
.add-user__close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
}
.add-user__title {
    margin: 0;
}
.add-user__save-btn {
    display: block;
    margin-right: auto;
}
.add-user .select {
    width: 42%;
}
.add-user__error-message {
    display: block;
    margin-top: 5px;
    color: red;
}

.close-btn {
    cursor: pointer;
    background: none;
    border: none;
    width: 20px;
    height: 20px;
}
.clone-btn:hover {
    opacity: 0.8;
}
.close-btn::after {
    display: block;
    content: '';
    width: 22px;
    height: 1px;
    background-color: #000;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    position: absolute;
    top: 7px;
    right: -3px;
}
.close-btn::before {
    display: block;
    content: '';
    width: 22px;
    height: 1px;
    background-color: #000;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
    position: absolute;
    top: 7px;
    right: -3px;
}
